.compare_contain {
    margin:85px 0 0;
    //background-color:red;
    .page_title {
        width:100%; height:100px; position:fixed; top:80px; background-color:$gray1; overflow:hidden;
        strong {
            display:block; text-align:center; padding:32px 0;
            font-size:36px;color:$gray7;font-family:$bebasNeue; font-weight:$regular;
        }
    }
    .results_row {
        width:100%;margin:0 auto;position:fixed; top:180px;
        .results_selected {
            height:92px;max-width:1200px;margin:0 auto;background-color:$white;
            @extend %clearfix;
            .re_txt {
                float:left; font-size:16px; color:$gray6; font-weight:$medium;
                em { color:$blue5; margin:0 5px 0 0; }
            }
            .comparison_btn {
                padding:40px 0 0; float:right;
                button {
                    display:block;font-size:13px; color:$gray7; font-weight:$medium;padding:10px 40px 10px 20px;
                    border:1px solid $gray3;position:relative;
                    @include borderRadius(100px);
                    &:after {
                        content:"";display:block;background:url(../../images/common/comparison_icon.png) 50% 50% no-repeat;
                        background-size:cover;width:13px; height:11px;position:absolute;right:17px;top:11px;
                    }
                }
            }
        }
        span {display:block;padding:50px 0 0;}
    }
    .compare_wrap {
        height:3000px;max-width:1200px; width:100%; margin:272px auto 0;
        section {
            height:235px;width:100%; display:flex; padding:20px 50px;margin:6px 0;
            background-color:#edf5ff; justify-content:space-between;
            @include boxSizing;
            //grid-template-rows: auto;
            .product_thumb {
                display:flex;
                .img_item {
                    background-color:$white;
                    @include borderRadius(10px);
                    img { height:100%; }
                }
                .substance {
                    padding:25px 0 0 30px;
                    span {
                        width:min-content;font-size:12px; color:$blue6;display:block;
                        padding:5px 12px;background-color:#e2e6fd;
                        @include borderRadius(10px);
                    }
                    h1 {padding:15px 0 0;font-size:24px;color:$gray7;}
                    p {padding:16px 0 0;font-size:14px;font-weight:$regular;color:$gray5; line-height:22px;}
                }

            }
            .skill_list {
                display:flex;
                text-align:center;
                align-items:center;
                & > div {
                    padding:0 20px;
                }
                p {padding:20px 0 0;font-size:14px;color:$gray6; line-height:16px;}
            }
        }
    }
    // popup
    .nha_common_compare {
        text-align:center; display:flex;
        .popup_content {
            max-width:1200px; background-color:$white;
            @include borderRadius(27px);
            .product_title_txt {
                strong {padding:45px 0 0;display:block;font-size:30px;color:$gray7;}
                p {padding:12px 0 0;color:$gray5;font-size:16px;font-weight:$regular;}
            }
            .product_thumb_list {
                padding:45px 80px 40px;
                ul {display:flex;justify-content:center;}
                .img_item {
                    width:59.34%;margin:0 auto;background-color:#f5f5f5;
                    @include borderRadius(10px);
                }
                img {width:100%;}
                strong {display:block;padding:20px 0 0;font-size:26px;color:$gray7;}
                p {padding:10px 0 0;font-size:14px;color:$gray5;}
                .btn_view {
                    padding:25px 0 0;display:flex;justify-content:center;
                    button {
                        width:106px;border:1px solid $gray3;font-size:13px;display:block;padding:13px 0; font-weight:$medium;
                        @include borderRadius(50px);
                        @include boxSizing;
                        &:nth-of-type(1) {color:$gray7;}
                        &:nth-of-type(2) {border:none;color:$white;background-color:$blue5;margin:0 0 0 4px;}
                    }
                }
            }
            .explanation_list {
                padding:10px 10px 30px; border-top:1px solid #e5e5e5;
                .scroll_wrap {padding:0 70px 20px;overflow-y:scroll;height:290px;}
                .desc_cont {
                    padding:30px 0 0;text-align:left;
                    &.operation_item {
                        span {
                            color:$gray7; position:relative;
                            &:after {
                                width:11px; height:10px;content:""; display:block;position:absolute;top:5px;right:-16px;
                                background:url(../../images/common/operation_icon.png) 50% 50% no-repeat; background-size:cover;
                            }
                        }
                        li {font-weight:$bold;}
                    }
                }
                .description {font-size:14px; color:$gray5; font-weight:$medium; }
                ul {
                    display:flex;
                    p { color:$gray7; font-size:14px;font-weight:$medium; }
                }
                li {
                    width:100%; font-size:14px; padding:20px 0;line-height:20px;
                    display:flex;justify-content:center;align-items:center;
                    text-align:center;background-color:#f5f5f5;margin:10px 0 0 5px; font-weight:$regular;
                    &:first-of-type {margin:10px 0 0;}
                }
            }
        }
        .btn_popup_close {
            width:20px; height:20px; background-image:url(../../images/common/close_x_btn.png); right:26px; top:28px;
        }
    }
}

